<template>
    <div class="page_main">
        <el-form ref="form" class="form" :model="form" label-width="80px">
            <el-form-item label="姓名" class="left">
                <el-input v-model="form.name"  placeholder="请输入姓名"></el-input>
            </el-form-item>
            <el-form-item label="卡号"  class="left">
                <el-input v-model="form.card" placeholder="请输入卡号"></el-input>
            </el-form-item>
            <el-form-item  class="left">
                <el-button type="primary" plain>查询</el-button>
                <el-button type="primary" plain>高级查询</el-button>
                <el-button type="primary" plain>今日门诊</el-button>
            </el-form-item>
        </el-form>
        <div class="childInfo">
            <span>{{childName}}</span>
            <span>{{childSex}}</span>
            <span>&nbsp;卡号：{{childCardNum}}</span>
            <span>&nbsp;出生日期：{{childBirth}}</span>
            <span>&nbsp;母亲：{{childMother}}</span>
            <span>&nbsp;电话：{{phone}}</span>
            <span>&nbsp;孕周：{{pregnantWeeks}}</span>
            <span>&nbsp;出生体重：{{weight}}</span>
            <span>&nbsp;出生身长：{{bodyLength}}</span>
            <span style="color:#409EFF">&nbsp;&nbsp;&nbsp;&nbsp; 实足月龄：{{monthAge}}</span>
        </div>
        <el-row style="height:80%">
            <el-col :span="12" class="colLeft">
                <el-table :data="tableData" border :header-cell-style="{background:'#FAFAFA',height:'48px'}" size='' style="width: 100%">
                    <el-table-column prop="userName" label="病例名称" width=""></el-table-column>  
                    <el-table-column prop="personNo" label="类别" width=""></el-table-column>
                    <el-table-column prop="scaleName" label="最近更新时间" width="">
                         <!-- <template slot-scope="subTime">
                                        {{subTime.row.stime.substring(0,10)}}
                                    </template> -->
                    </el-table-column>
                    <el-table-column prop="userName" label="状态" width=""></el-table-column>  
                </el-table>
                <div class="page_bottom">
                    <div class="block">
                        <el-pagination
                        background
                        :hide-on-single-page="value"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage4"
                        :page-size="10"
                        layout="total, prev, pager, next, jumper"
                        :total="total">
                        </el-pagination>
                    </div>
                    <div class="left bottom_btn">
                        <el-button type="primary" plain  size="small" class="right" @click="buildBoard()">新建病例</el-button>
                    </div>
                </div>
            </el-col>
            <el-col :span="12" class="colRight" >
                <el-table
                    :data="tableProInfoData"
                    :span-method="objectSpanMethod"
                    size='small'
                    :header-cell-style="{background:'#FAFAFA',height:'48px'}"
                    border
                    style="width: 100%;">
                    <el-table-column prop="id" label="" width=""></el-table-column>
                    <el-table-column prop="name" label="专案名称"></el-table-column>
                    <el-table-column prop="date" label="立案日期"></el-table-column>
                    <el-table-column prop="address" label="状态"></el-table-column>
                </el-table>
                <el-table
                    :data="tableaBnormalData"
                    :span-method="objectSpanMethod"
                    size='small'
                    :header-cell-style="{background:'#FAFAFA',height:'48px'}"
                    border
                    style="width: 100%;margin-top:20px;">
                    <el-table-column prop="id" label="" width=""></el-table-column>
                    <el-table-column prop="name" label="异常信息"></el-table-column>
                    <el-table-column prop="date" label="病例名称"></el-table-column>
                    <el-table-column prop="address" label="创建时间"></el-table-column>
                    <el-table-column prop="date" label="异常状态"></el-table-column>
                    <el-table-column prop="address" label="取消时间"></el-table-column>
                </el-table> 
                <el-table
                    :data="tableaHighrisk"
                    :span-method="objectSpanMethod"
                    :header-cell-style="{background:'#FAFAFA',height:'48px'}"
                    size='small'
                    border
                    style="width: 100%;margin-top:20px;">
                    <el-table-column prop="id" label="" width=""></el-table-column>
                    <el-table-column prop="name" label="名称"></el-table-column>
                    <el-table-column prop="date" label="类别"></el-table-column>
                </el-table>
                <div  class="tableBtn" >
                    <el-button type="primary" plain  size="small" @click="appraisal()">测 评</el-button>
                    <el-button type="primary" plain  size="small" @click="proManage()">专案管理</el-button>
                </div>
            </el-col>
        </el-row>
        <el-dialog
            title="选择模板"
            :visible.sync="dialogBoard"
            width="50%"
            >
            <el-form ref="formBoard" class="formBoard" :model="formBoard">
                <el-form-item label="" class="left marginR">
                    <el-input v-model="formBoard.name"  placeholder="请输入模板名称"></el-input>
                </el-form-item>
                <el-form-item label=""  class="left marginR">
                    <!-- <el-input v-model="formBoard.card" placeholder="请输入模板分类"></el-input> -->
                    <el-select v-model="formBoard.check" clearable  placeholder="模板分类">
                            <el-option label="检查类" value="检查类"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item  class="left">
                    <el-button type="primary" plain>搜索</el-button>
                </el-form-item>
            </el-form>
            <el-table  :data="tableBoardData"  style="width: 100%" height="250">
                <el-table-column prop="date" label="模板名称" width=""></el-table-column>
                <el-table-column prop="name" label="模板分类"  width=""></el-table-column>
                <el-table-column prop="address" label="创建时间"></el-table-column>
                <el-table-column prop="address" label="审核状态"></el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogBoard = false">取 消</el-button>
                <el-button type="primary" @click="dialogBoard = false">确定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    components: {},
    data(){
        return{
            form: {
                name: '',
                card: ''
             }, 
            childName: '李晓刀',
            childSex: '男',
            childCardNum: '494190912020050900001',
            childBirth: '2020-01-01',
            childMother: '王晓',
            phone: '13622335566',
            pregnantWeeks: '39周',
            weight: '3.00kg',
            bodyLength: '50.1cm',
            monthAge: '5月20天',
            tableData: [
               {
                      userName: '1111',
                      personNo: '1111',
                      scaleName: '1111',
                      userName: '1111',
               }
           ],
           currentPage4: 1,
            total: 0,
            pageNumber: 1,
            pageSize: 10,
            value: false,
            dialogBoard: false,
            formBoard: {
                name: '',
                check: ''
             }, 
             tableBoardData: [
                {
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市'
                }, {
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市'
                }, {
                    date: '2016-05-01',
                    name: '王小虎',
                    address: '上海市'
                }, {
                    date: '2016-05-03',
                    name: '王小虎',
                    address: '上海市普'
                }
             ],
             tableProInfoData: [     
                {
                    id: '专案信息',
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市'
                }, {
                    id: '专案信息',
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市'
                }, 
                 
             ],
             tableaBnormalData: [
                {
                    id: '异常信息',
                    date: '2016-05-02',
                    name: '王小虎',
                    address: '上海市'
                }, {
                    id: '异常信息',
                    date: '2016-05-04',
                    name: '王小虎',
                    address: '上海市'
                }, 
             ],
             tableaHighrisk: [
                 {
                    id: '高危因素',
                    date: '2016-05-02',
                    name: '王小虎',
                }, {
                    id: '高危因素',
                    date: '2016-05-04',
                    name: '王小虎',
                },
             ]
        }
    },
    created(){

    },
    methods:{
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
           
           
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            
        },
        buildBoard(){
            this.dialogBoard = true
        },
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
        if (columnIndex === 0) {
          if (rowIndex % 2 === 0) {
            return {
              rowspan: 2,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      }
    }
}
</script>
<style lang="less" scoped>
.page_main{
    width: 100%;
    height: 100%;
    .form{
        height: 66px;
        margin-top: 20px;
    }
    .left{
        float: left;
    }
    .right{
        float: right;
    }
    .childInfo{
        font-size: 14px;
        margin-bottom:10px;
    }
    .block{
        float: left;
        margin-top: 20px;
    }
    .bottom_btn{
        margin: 20px;
    }
    .page_bottom{
        height: 70px;
        width: 50%;
        position: absolute;
        bottom: 0;
    }
    .formBoard{
        height: 60px;
    }
    .marginR{
        margin-right: 10px;
    }
    .colLeft{
        height:100%;
        border-top:8px solid #F0F2F5;
        border-right:3px solid #F0F2F5 ;
        padding:4px;
    }
    .colRight{
        height:100%;
        border-top:8px solid #F0F2F5;
        border-left:3px solid #F0F2F5;
        padding:4px;
    }
    .tableBtn{
        margin-top: 50px;
        text-align: right;
    }
}
</style>





